import React, { forwardRef } from 'react';
import classNames from 'classnames';
import './index.less';

interface TagProps {
  /**
   * 标签颜色
   * @default 标签颜色
   */
  color?: string;
  /**
   * 标签内容
   * @default 标签内容
   */
  children: any;
  /**
   * 是否带边框
   * @default 是否带边框
   */
  bordered?: boolean;
  /**
   * 浅色背景,深色背景时，文字颜色为白色；浅色背景时，文字颜色为灰色。
   * @default 浅色背景
   */
  light?: boolean;
}

const Tag = forwardRef((props: TagProps, ref) => (
  <span
    // @ts-ignore
    ref={ref}
    style={{ background: props.color }}
    className={classNames(
      'god-tag',
      (props.light || props.bordered) && 'tag-light',
      props.bordered && 'tag-bordered'
    )}
  >
    {props.children}
  </span>
));

export default Tag;
